<template>
  <div class="martial container-fluid">
    <div class="row">
      <p class="my-3 text-start">
        <a
          href="https://forms.gle/kohSceNEt6JWjrj2A"
          target="_blank"
          class="text-start"
          ></a
        >
      </p>
    </div>
    <div class="row g-0">
      <p class="text-start">
        <button
          class="btn btn-danger btn-sm btn-copy d-none d-sm-inline my-3"
          v-on:click="copySimpleCompileResult()"
        >
          
        </button>
        <button
          class="btn btn-danger btn-copy d-inline d-sm-none my-3"
          v-on:click="copySimpleCompileResult()"
        >
          
        </button>
        <br />
        {{ searchDateTimeDisplayText() }}
      </p>
      <p
        id="simpleCompileResult"
        class="text-start"
        v-html="getSimpleCompileResult(compileResult)"
      ></p>
    </div>
    <div class="row g-0">
      <div
        v-for="(names, martial) in compileResult"
        v-bind:key="martial"
        class="card col-6 col-sm-4 col-md-3 border-0"
      >
        <div class="card-body text-start">
          <h6 class="card-title" v-bind:class="getMartialCountryColor(martial)">
            {{ martial }}:
          </h6>
          <p class="text-break text-start">{{ names }}</p>
        </div>
      </div>
    </div>
    <div class="row g-0">
      <p class="mb-3 text-start">{{ searchDateTimeDisplayText() }}</p>
      <!--  -->
      <div class="text-start d-none d-sm-block">
        <flatPickr
          ref="startDateTimePicker"
          v-model="startDateTime"
          v-bind:config="flatPickrConfig"
          placeholder=""
          class="flatPickr mb-3"
        />
        <flatPickr
          ref="startDateTimePicker"
          v-model="endDateTime"
          v-bind:config="flatPickrConfig"
          placeholder=""
          class="flatPickr mb-3 ms-3 ms-sm-5"
        />
        <button
          class="btn btn-primary btn-search btn-sm d-none d-sm-inline ms-sm-3"
          v-on:click="search()"
        >
          
        </button>
      </div>
      <!--  -->
      <div class="text-start d-block d-sm-none">
        <flatPickr
          ref="startDateTimePicker"
          v-model="startDateTime"
          v-bind:config="flatPickrConfig"
          placeholder=""
          class="flatPickr"
        />
        <br />
        <flatPickr
          ref="startDateTimePicker"
          v-model="endDateTime"
          v-bind:config="flatPickrConfig"
          placeholder=""
          class="flatPickr my-3"
        />
        <br />
        <div class="text-center">
          <button class="btn btn-primary btn-search my-2" v-on:click="search()">
            
          </button>
        </div>
      </div>
      <div class="table-wrapper">
        <table class="table">
          <thead>
            <tr>
              <th
                v-for="(title, index) in getTitles()"
                v-bind:key="index"
                scope="col"
                class="d-none d-sm-table-cell text-nowrap"
              >
                {{ title }}
              </th>
              <th
                v-for="(title, index) in getAlternativeTitles()"
                v-bind:key="index"
                scope="col"
                class="d-table-cell d-sm-none text-nowrap"
              >
                {{ title }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(datum, index) in data" v-bind:key="index">
              <th scope="row">{{ datum[0] }}</th>
              <td>{{ datum[1] }}</td>
              <td v-bind:class="getMartialCountryColor(datum[2])">
                {{ getMartialValue(datum[2]) }}
              </td>
              <td v-bind:class="getMartialCountryColor(datum[3])">
                {{ getMartialValue(datum[3]) }}
              </td>
              <td v-bind:class="getMartialCountryColor(datum[4])">
                {{ getMartialValue(datum[4]) }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="position-fixed bottom-0 start-50 translate-middle-x p-3">
      <div
        ref="copyToast"
        class="toast hide"
        role="alert"
        aria-live="assertive"
        aria-atomic="true"
      >
        <div class="toast-header">
          <strong class="me-auto"></strong>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="toast"
            aria-label="Close"
          ></button>
        </div>
        <div class="toast-body"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { getMartialAuctionResult } from '@/services/api';
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import { MandarinTraditional } from 'flatpickr/dist/l10n/zh-tw.js';
import moment from 'moment-timezone';

export default {
  name: 'MartialView',
  components: {
    flatPickr,
  },
  setup: () => {
    let data = ref([]);
    let compileResult = ref({});
    // let toast = undefined;
    const flatPickrConfig = {
      locale: MandarinTraditional,
      disableMobile: true,
      enableTime: true,
      dateFormat: 'Y-m-d H:i',
      time_24hr: true,
    };
    const startDateTime = ref(null);
    const endDateTime = ref(null);
    const route = useRoute();

    const getResult = async (start, end) => {
      return await getMartialAuctionResult(start, end);
    };

    const getTitles = () => {
      return [
        '',
        '',
        '1',
        '2',
        '3',
      ];
    };

    const getAlternativeTitles = () => {
      return ['', 'ID', '#1', '#2', '#3'];
    };

    const getMartialValue = (value) => {
      if (!value) {
        return 'X';
      }

      const validPattern = /^[][-]/i;

      if (validPattern.test(value)) {
        return value;
      } else {
        return 'X';
      }
    };

    const getMartialCountryColor = (value) => {
      if (!value) {
        return '';
      }

      if (value.startsWith('')) {
        return 'color-wei';
      } else if (value.startsWith('')) {
        return 'color-shu';
      } else if (value.startsWith('')) {
        return 'color-wu';
      } else if (value.startsWith('')) {
        return 'color-heros';
      } else if (value.startsWith('')) {
        return '';
      } else {
        return '';
      }
    };

    const getCompileResult = (results) => {
      const map = {};

      // 
      // const list = results.map((r) => {
      //   return {
      //     time: r[0],
      //     id: r[1],
      //     martial1: getMartialValue(r[2]),
      //     martial2: getMartialValue(r[3]),
      //     martial3: getMartialValue(r[4]),
      //   };
      // });

      // 
      const distinctMap = results.reduce((r1, r2) => {
        r1[r2[1]] = {
          time: r2[0],
          id: r2[1],
          martial1: getMartialValue(r2[2]),
          martial2: getMartialValue(r2[3]),
          martial3: getMartialValue(r2[4]),
        };
        return r1;
      }, {});

      const list = Object.values(distinctMap);

      // 1
      for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (map[item['martial1']]) {
          map[item['martial1']] += ', ' + item.id;
        } else {
          map[item['martial1']] = item.id;
        }
      }

      // 2
      for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (map[item['martial2']]) {
          map[item['martial2']] += ', ' + item.id;
        } else {
          map[item['martial2']] = item.id;
        }
      }

      // 3
      for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (map[item['martial3']]) {
          map[item['martial3']] += ', ' + item.id;
        } else {
          map[item['martial3']] = item.id;
        }
      }

      // 
      delete map['X'];
      return map;
    };

    const getSimpleCompileResult = (compileResult) => {
      let simpleCompileResult = '';
      for (let martial in compileResult) {
        const names = compileResult[martial];
        simpleCompileResult += martial;
        simpleCompileResult += '';
        simpleCompileResult += names;
        simpleCompileResult += ';<br />';
      }
      return simpleCompileResult;
    };

    const formatDateTime = (dateTime) => {
      if (dateTime) {
        return moment(dateTime).format('YYYYMMDD HHmm');
      }
      return '';
    };

    const searchDateTimeDisplayText = () => {
      if (startDateTime.value && endDateTime.value) {
        return (
          formatDateTime(startDateTime.value) +
          '' +
          formatDateTime(endDateTime.value) +
          ''
        );
      }

      if (startDateTime.value) {
        return formatDateTime(startDateTime.value) + '';
      }

      if (endDateTime.value) {
        return formatDateTime(endDateTime.value) + '';
      }

      return '';
    };

    const copySimpleCompileResult = () => {
      //  textarea
      const textArea = document.createElement('textarea');
      const copyText = document.getElementById('simpleCompileResult');
      let text = `${searchDateTimeDisplayText()}\n${
        copyText.textContent
      }`;
      text = text.replaceAll(';', ';\n');
      textArea.value = text;
      document.body.appendChild(textArea);
      //  iOS 
      if (navigator.userAgent.match(/ipad|iphone/i)) {
        const range = document.createRange();
        range.selectNodeContents(textArea);
        const selection = window.getSelection();
        selection.removeAllRanges();
        selection.addRange(range);
        textArea.setSelectionRange(0, 999999);
      } else {
        textArea.select();
      }

      document.execCommand('Copy');
      document.body.removeChild(textArea);
      window.alert('');
    };

    const search = () => {
      getResult(startDateTime.value, endDateTime.value).then((result) => {
        if (!result) {
          return;
        }
        data.value = result.values;
        compileResult.value = getCompileResult(data.value);
      });
    };

    const getQueryStringTime = (time, type) => {
      let t = moment(time, 'YYYYMMDDHHmm');

      if (t.isValid()) {
        return t.format('YYYY-MM-DD HH:mm');
      }

      t = moment().tz('Asia/Taipei');
      if (type === 'start') {
        t.set('hour', 0);
        t.set('minute', 0);
      }
      if (type === 'end') {
        t.set('hour', 23);
        t.set('minute', 59);
      }
      return t.format('YYYY-MM-DD HH:mm');
    };

    onMounted(() => {
      const query = route.query;
      // toast = new Toast(getCurrentInstance().ctx.$refs.copyToast);
      startDateTime.value = getQueryStringTime(query.start, 'start');
      endDateTime.value = getQueryStringTime(query.end, 'end');
      search();
    });

    return {
      data,
      compileResult,
      flatPickrConfig,
      startDateTime,
      endDateTime,
      getTitles,
      getAlternativeTitles,
      getMartialValue,
      getMartialCountryColor,
      getSimpleCompileResult,
      searchDateTimeDisplayText,
      copySimpleCompileResult,
      search,
    };
  },
};
</script>

